@import '../../sass/variables';
@import '../../sass/grid.scss';
@import '../../sass/mixins.scss';

.composition {
    position: relative;

    &__photo {
        width: 55%;
        box-shadow: 0 1.5rem 4rem rgba($color-black, .4);
        border-radius: 2px;
        position: absolute;
        z-index: 10;
        transition: all .2s;
        outline-offset: 1rem;
        

        @include respond(tab-port){
            float: left;
            position: relative;
            width: 33.33333%;
            box-shadow: 0 1.5rem 3rem rgba(black, .2);
        }

        &--p1 {
            left: 0;
            top: -2rem;
            @include respond(tab-port){
                transform: scale(1.2);
                top: 0 ;
            }
        }

        &--p2 {
            right: 0;
            top: 2rem;
            @include respond(tab-port){
                top: -1rem ;
                transform: scale(1.3);
                z-index: 100;
            }
        }

        &--p3 {
            left: 20%;
            top: 10rem;

            @include respond(tab-port){
                top: 1rem;
                left: 0;
                transform: scale(1.1);
            }
        }

        &:hover {
         //   outline: 0.2rem solid #999;
            transform: scale(1.05) translateY(-.5rem);
            box-shadow: 0 2.5rem 4rem rgba($color-black, .5);
            z-index: 20;
        }
    }

    &:hover &__photo:not(:hover) {
        transform: scale(.95);
    }
    //composition:hover composition__photo:not(:hover) {

   
}


.about{
    background-color: $color-grey-light-1;
    padding: 25rem 0;
    margin-top: -20vh;

    @include respond(tab-port){
        padding: 20rem 0;
    }
}

.social{
    border-bottom: 3px solid #999;
    padding-bottom: 0.5rem;
    width: fit-content;
    margin: 0 auto;
    cursor: pointer;
    
    .icon{
        font-size: 32px;
        margin: 1rem;
        fill: rgb(188, 3, 203);

        @media(max-width: 500px){
            margin: 0.4rem;
        }
    }
}

.pill-btn{
text-transform: uppercase;
color: white;
padding: 15px 40px;
display: inline-block;
transition: all .2s;
text-decoration: none;
border-radius: 100px;
position: relative;
border: none;
cursor: pointer;
background-color: purple;
margin-top:10px;
}

.paragraph{
    text-align: center;
    word-break: normal;
    word-spacing: 2px;
    letter-spacing: 0.5px;
    font-size: 16px;
}