  .footer-distributed{
	  background-color: rgba(255, 255, 255, 0.8);
	  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
	  box-sizing: border-box;
	  width: 100%;
	  text-align: left;
	  font: bold 16px sans-serif;
	  padding: 55px 50px;
	  color: black;
  }
  
  .footer-distributed .footer-left,
  .footer-distributed .footer-center,
  .footer-distributed .footer-right{
	  display: inline-block;
	  vertical-align: top;
  }
  
  /* Footer left */
  
  .footer-distributed .footer-left{
	  width: 40%;
  }
  
  /* The company logo */
  
  .footer-distributed h3{
	  color: black;
	  font: normal 36px 'Open Sans', cursive;
	  margin: 0;
  }
  
  .footer-distributed h3 span{
	  color:  lightseagreen;
  }
  
  /* Footer links */
  
  .footer-distributed .footer-links{
	  color: #777;
	  font-weight: bold;
	  margin: 20px 0 12px;
	  padding: 0;
  }
  
  .footer-distributed .footer-links a{
	  display:inline-block;
	  line-height: 1.8;
	  font-weight:400;
	  text-decoration: none;
	  color:  inherit;
  }
  
  .footer-distributed .footer-company-name{
	  color: rgb(105, 105, 105);
	  font-size: 14px;
	  font-weight: normal;
	  margin: 0;
  }
  
  /* Footer Center */
  
  .footer-distributed .footer-center{
	  width: 35%;	 
  }



  
  .footer-distributed .footer-center i{
	  background-color:  #33383b;
	  color: black;
	  font-size: 25px;
	  width: 38px;
	  height: 38px;
	  border-radius: 50%;
	  text-align: center;
	  line-height: 42px;
	  margin: 10px 15px;
	  vertical-align: middle;
  }
  
  
  .footer-distributed .footer-center p{
	  display: inline-block;
	  color: rgb(105, 105, 105);
	  font-weight:400;
	  vertical-align: middle;
	  margin:0;
  }
  
  .footer-distributed .footer-center p span{
	  display:block;
	  font-weight: normal;
	  font-size:14px;
	  line-height:2;
  }
  
  .footer-distributed .footer-center p a{
	  color:  lightseagreen;
	  text-decoration: none;;
  }
  
  .footer-distributed .footer-links a:before {
	content: "|";
	font-weight:300;
	font-size: 20px;
	left: 0;
	color: #fff;
	display: inline-block;
	padding-right: 5px;
  }
  
  .footer-distributed .footer-links .link-1:before {
	content: none;
  }
  
  /* Footer Right */
  
  .footer-distributed .footer-right{
	  width: 20%;
  }
  
  .footer-distributed .footer-company-about{
	  line-height: 20px;
	  color: rgb(105, 105, 105);
	  font-size: 13px;
	  font-weight: normal;
	  margin: 0;
  }
  
  .footer-distributed .footer-company-about span{
	  display: block;
	  color: rgb(105, 105, 105);
	  font-size: 14px;
	  font-weight: bold;
	  margin-bottom: 20px;
  }
  
  .footer-distributed .footer-icons{
	  margin-top: 25px;
  }
  
  .contact-icon{
	  margin-right: 5px;
	  font-size: 16px;
  }
  
  .footer-social-icons{
	  display: flex;
	  flex-direction: row;
	  margin-top: 1.5rem;
  
	  @media(max-width: 900px){
		  justify-content: center;
	  }
  }
  
  .social-icon{
	  margin-right: 10px;
	  color: rgb(188, 3, 203);
	  margin-bottom: 0.5rem;
  }
  
  .address-icon{
	  display: flex;
	  align-items: center;
	  color: rgb(188, 3, 203);

	  .mail{
		text-transform: lowercase;
	  }
  }
  
  
  /* If you don't want the footer to be responsive, remove these media queries */
  @media (max-width: 880px) {
	  .footer-distributed{
		  font: bold 14px sans-serif;
	  }
	  .footer-distributed .footer-left,
	  .footer-distributed .footer-center,
	  .footer-distributed .footer-right{
		  display: block;
		  width: 100%;
		  margin-bottom: 40px;
		  text-align: center;
	  }
  
	  .footer-distributed .footer-center i{
		  margin-left: 0;
	  }
  
	  .footer-distributed .footer-center{
		  display: flex;
		  flex-direction: column;
		  justify-content: center;
		  align-content: center;
		  margin-bottom: 2rem;
	  }


	  .footer-distributed{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-content: center;
	}
  }

  @media(max-width:600px){
	.footer-distributed .footer-center{
		width: auto
	}
  }
  
.form-container{
	.pill-btn{
		text-transform: uppercase;
		color: white;
		padding: 15px 40px;
		display: inline-block;
		transition: all .2s;
		text-decoration: none;
		border-radius: 100px;
		position: relative;
		border: none;
		cursor: pointer;
		background-color: purple;
		margin-top:10px;
	}
}